@import "../../app/app-variables";
@import "../../app/app";
@import "../../categories/categories";

div.category-random {
    div.drink {
        span.drink-nr {
            // @include circle-border-shadow-gradient($cat-random-c1, $cat-random-c2, $gr-ll * 1em);
            border: ($gr-ll * 1em) solid $cat-random-c1;
            color: $cat-random-c1;
        }
    }

    #drinks-random-drawing {
        align-content: center;
        display: flex;
        height: $gr-h * 100%;

        // svg {
        //     height: 100%;
        //     width: 100%;
        // }
    }
}

div.drinks-random {
    align-items: center;
    display: flex;
    height: $gr-l * 100%;
    justify-content: center;
    position: relative;

    button.btn {
        align-items: center;
        background-image: $nav-bg;
        background-color: $navc3;
        border-color: $navc2;
        border-radius: 17px;
        color: $cl;
        display: flex;
        font-size: 100%;
        height: $gr-l * 100%;
        justify-content: center;
        width: $gr-h * 100%;
    }

    img.drinks-random {
        height: 100%;
        position: absolute;
        transform: scale($gr-h);
        width: auto;
    }

    img.drinks-random:first-of-type {
        left: 0;
    }

    img.drinks-random:last-of-type {
        right: 0;
    }
}
