@import "../app/app-variables";
@import "../app/app";
@import "../categories/_categories";
@import "./_kbrd.scss";

// https://codepen.io/giumagnani/pen/jBNJKw

$c-inactive: $cld;
$c-border-inactive: $cld;
$kbrd-btn-bg-color: $cll;
$kbrd-btn-border-color: gray;
$kbrd-bg-c1: rgb(0, 87, 147);
$kbrd-bg-c2: rgb(61, 188, 235);
$kbrd-bg: $kbrd-bg-c1;

div.kbrd {
    // background-image: kbrdBg(silver, lavender);
    background: $kbrd-bg;
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    font-family: system-ui, -apple-system, -apple-system-font, "Segoe UI", "Roboto", sans-serif;
    justify-content: space-evenly;
    overflow: hidden;
    position: relative;

    span.kbrd-btn {
        background-color: $kbrd-btn-bg-color;
        border-color: $kbrd-btn-border-color;
        border-radius: $gr-ll * 1em;
        border-style: solid;
        border-width: ($gr-lllh * 1em);
        box-sizing: border-box;
        font-size: 90%;
        height: 100%;
        margin: 0 ($gr-llh * 1em);
        min-width: $gr-llh * 100%;
        max-width: $gr-lh * 100%;
        text-align: center;

        &:active {
            transform: translateY(4px);
        }
    }

    span.kbrd-inactive {
        color: $c-inactive;
        border-color: $c-border-inactive;
        opacity: $gr-l;

        &:active {
            box-shadow: none;
            transform: none;
        }
    }

    div.kbrd-row {
        display: flex;
        min-height: 15%;
        justify-content: space-around;
        position: relative;
        width: 100%;
    }

    div.kbrd-row-top,
    div.kbrd-row-btm {
        span.kbrd-btn:first-of-type,
        span.kbrd-btn:last-of-type {
            max-width: $gr-lh * 100%;
            width: $gr-lh * 100%;
        }
    }

    div.kbrd-row-top {
        span.kbrd-txt {
            background: none;
            background-color: $cl;
            padding: 0 ($gr-l * 1em);
            max-width: $gr-h * 100%;
            width: $gr-h * 100%;

            &:active {
                transform: none;
            }
        }
    }

    div.kbrd-row-btm {
        span.kbrd-btn:nth-child(even) {
            max-width: ($gr-l + $gr-llh) * 100%;
            width: ($gr-l + $gr-llh) * 100%;
        }
    }
}

div.kbrd-digipad {
    span.kbrd-btn {
        flex-grow: 3;
    }

    div.kbrd-row-top {
        span.kbrd-txt {
            max-width: $gr-lh * 100%;
            width: $gr-lh * 100%;
        }
    }

    div.kbrd-row-btm {
        span.kbrd-btn:nth-child(even) {
            max-width: ($gr-lh) * 100%;
            width: ($gr-lh) * 100%;
        }

        span.kbrd-btn:nth-child(odd) {
            visibility: hidden;
        }
    }
}
