@import "../../app/app-variables";
@import "../../app/app";
@import "../categories";
@import "./cat-icon";

div.category-icon {
    display: flex;
    justify-content: center;
    overflow: hidden;
    position: relative;

    span.category-icon {
        align-items: center;
        bottom: 0;
        display: flex;
        height: 100%;
        justify-content: center;
        position: absolute;
        width: 100%;

        img.category-icon {
            border-radius: 50%;
            height: 100%;
            position: absolute;
        }

        svg.category-icon {
            height: $gr-hh * 100%;
            position: absolute;
        }
    }

    span.cat-alco {
        svg.cat-alco {
            fill: $cat-alco-c2;
            height: 50%;
        }
    }

    span.cat-custom {
        img.cat-custom {
            // background-image: catIconBg($cat-custom-c1, $cat-custom-c2);
            background: $cat-custom-c1;
        }
    }

    span.cat-list {
        svg.cat-list {
            fill: $cat-list-c1;
        }
    }

    span.cat-prefs {
        img.cat-prefs {
            // background-image: catIconBg($cdd, $cd);
            background: $cd;
        }

        svg.category-icon {
            height: $gr-hl * 100%;
        }
    }

    span.cat-random {
        img.cat-random {
            // background-image: catIconBg($cdd, $cd);
            background: $cd;
        }

        svg.category-icon {
            height: $gr-h * 100%;
        }
    }

    span.cat-top {
        img.cat-top {
            // background-image: catIconBg($cat-top-c2, $cat-top-c1);
            background: $cat-top-c1;
        }
    }
}
