@import "../../app/app-variables";
@import "../../app/app";
@import "../../categories/_categories";

ul.ings-custom {
    li.ings-custom {
        div.ing-custom {
            align-content: center;
            align-items: center;
            display: flex;
            flex-wrap: wrap;
            height: 100%;
            justify-content: center;
            width: 100%;

            p.ing-custom {
                height: $gr-l * 100%;
                width: 100%;
            }

            span.ing-custom {
                border-radius: 50%;
                border: ($gr-lll * 1em) solid #7d8499; // gray;
                height: $gr-h * 1em;
                margin: 0 ($gr-l * 1em);
                width: $gr-h * 1em;
            }

            span.ing-custom-selected {
                background-color: #ff85a4; // orchid;
            }

            span.ing-custom-hidden {
                visibility: hidden;
            }
        }

        div.ing-custom-inactive {
            background: $bg-inactive;
            color: $c-inactive;
        }
    }
}
