button.btn {
    --border-width: 3px;
    border-radius: var(--border-width);
    color: $navc3;
    font-size: 62%;
    font-weight: bold;
    overflow: hidden;
    // padding: $gr-l * 1em;
    position: relative;
    letter-spacing: $gr-lll * 1em;

    &:after {
        // animation: shine 3s ease infinite;
        content: "";
        position: absolute;
        top: 0;
        left: -200%;
        width: 200%;
        height: 100%;
        transform: skewX(-20deg);
        background-image: linear-gradient(to right, transparent, rgba(#fff, 0.3), transparent);
    }

