@import "../../app/app-variables";
@import "../../app/app";
@import "../_categories";

// skos
// background: -webkit-gradient(linear, 0% 0%, 0% 100%, from(rgba(255, 255, 255, .15)), to(rgba(0, 0, 0, .25))), -webkit-gradient(linear, left top, right bottom, color-stop(0, rgba(255, 255, 255, 0)), color-stop(0.5, rgba(255, 255, 255, .1)), color-stop(0.501, rgba(255, 255, 255, 0)), color-stop(1, rgba(255, 255, 255, 0)));

div.category {
    height: 100%;
    overflow-x: hidden;
    overflow-y: auto;
}

div.category::-webkit-scrollbar {
    display: none;
}

div.category-aside-narrow {
    width: 100% - $cat-narr-w;
}

div.category-aside-wide {
    width: 100% - $cat-wide-w;
}

//

div.category-list {
    ul.drinks-list {
        li.drinks-list {
            span.drinks-list-nr {
                // @include circle-border-shadow-gradient($cat-list-c1, $cat-list-c2, $gr-ll * 1em);
                border: ($gr-ll * 1em) solid $cat-list-c1;
            }
        }

        li.drinks-list:nth-of-type(even) {
            span.drinks-list-nr {
                color: $cd;
            }
        }
    }
}

div.category-prefs {
    ul.drinks-list {
        div.drink {
            width: 100%;
        }

        li.drinks-list {
            span.drinks-list-nr {
                // @include circle-border-shadow-gradient($cat-prefs-c1, $cat-prefs-c2, $gr-ll * 1em);
                border: ($gr-ll * 1em) solid $cat-prefs-c1;
            }
        }

        li.drinks-list:nth-of-type(even) {
            span.drinks-list-nr {
                color: $cd;
            }
        }

        li.drinks-list:only-child {
            padding: ($gr-ll * 100%) 0;
        }
    }
}

div.category-top {
    ul.drinks-list {
        li.drinks-list {
            span.drinks-list-nr {
                // @include circle-border-shadow-gradient($cat-top-c1, $cat-top-c2, $gr-ll * 1em);
                border: ($gr-ll * 1em) solid $cat-top-c1;
            }
        }

        li.drinks-list:nth-of-type(even) {
            span.drinks-list-nr {
                color: $cd;
            }
        }
    }
}

div.category-random {
    background: $bg-dark;
    color: $cl;

    div.drink {
        height: $gr-h * 100%;
        left: 0;
        margin: 0 auto;
        position: relative;

        div.drink-icon {
            width: $gr-l * 100%;
        }

        ul.drink-ings {
            width: ($gr-l + $gr-llh) * 100%;
        }
    }
}

