@import "../../app/app-variables";
@import "../../app/app";

div.drink-custom-glass {
    overflow: hidden;
    position: relative;
    width: 100%;

    svg.drink-custom-glass {
        top: 0;
        display: inline-block;
        height: 100%;
        position: absolute;
        transform: translateY(5%) scale(0.9);
        width: auto;

        g.drink-custom-glass-fill {
            fill: orchid;
            stroke: $cd;
            stroke-width: 1;
        }
    }
}
