@import "../../app/app-variables";
@import "../..//app/app";

div.drink {
  align-items: stretch;
  align-content: flex-start;
  // background-color: wheat;

  display: flex;
  flex-wrap: wrap;
  height: 100%;
  justify-content: flex-start;
  overflow: hidden;
  position: relative;

  span.drink-nr {
    align-items: center;
    border-radius: 50%;
    display: flex;
    // height: $grs-h * 1em;
    height: 3em;
    justify-content: center;
    right: $gr-llh * 100%;
    position: absolute;
    top: $gr-ll * 100%;
    // width: $grs-hh * 1em;
    width: 3em;
  }

  h2.drink-name {
    // background-color: mediumorchid;
    flex-shrink: 0;
    height: $gr-lh * 100%;
    position: relative;
    width: 100%;
  }

  div.drink-icon {
    // background-color: yellowgreen;
    display: block;
    height: $gr-hl * 100%;
    width: $gr-lh * 100%;
  }

  ul.drink-ings {
    // background-color: powderblue;
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    height: $gr-hl * 100%;
    list-style-type: disc;
    position: relative;
    width: $gr-h * 100%;
  }

  li.drink-ings {
    margin-left: $gr-llh * 100%;
    padding: $gr-lll * 1em 0;
    position: relative;
  }

  li.drink-ings-sep {
    display: none;
  }

  nav.drink {
    display: block;
    height: $gr-hl * 100%;
    text-align: center;
    width: $gr-ll * 100%;

    button.btn {
      background-image: $nav-bg;
      background-color: $navc3;
      border-color: $navc2;
      color: $cl;
      font-size: $gr-hh * 100%;
      font-weight: bold;
      height: $gr-h * 100%;
      line-height: $grs-h * 100%;
      top: $gr-h * 100%;
      transform: translateY(-50%);
      width: $gr-hh * 100%;
    }
  }
}
