@import "./app-variables";

#app-view-wrapper {
  h1,
  h2,
  h3,
  h4,
  h5,
  h6 {
    font-weight: normal;
  }

  h1 {
    font-size: $grs-h * 100%;
  }

  h2 {
    font-size: $grs-l * 100%;
  }

  h3 {
    font-size: $grs-lh * 100%;
  }

  h4 {
    font-size: $grs-ll * 100%;
  }

  h5 {
    font-size: $grs-llh * 100%;
  }

  h6 {
    font-size: $grs-lll * 100%;
  }

  .cursor {
    cursor: pointer;
  }

  //

  .textc {
    text-align: center;
  }

  //

  .center {
    left: 50%;
    margin-right: -50%;
    position: absolute;
    top: 50%;
    transform: translate(-50%, -50%);
  }

  .center-h {
    left: 50%;
    margin-right: -50%;
    position: absolute;
    transform: translateX(-50%);
  }

  .center-v {
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
  }

  .center-top-grh {
    left: 50%;
    margin-right: -50%;
    position: absolute;
    top: $gr-h * 100%;
    transform: translate(-50%, -50%);
  }

  .center-top-grl {
    left: 50%;
    margin-right: -50%;
    position: absolute;
    top: $gr-l * 100%;
    transform: translate(-50%, -50%);
  }

  .center-top-grlh {
    left: 50%;
    margin-right: -50%;
    position: absolute;
    top: $gr-lh * 100%;
    transform: translate(-50%, -50%);
  }

  .center-top-grll {
    left: 50%;
    margin-right: -50%;
    position: absolute;
    top: $gr-ll * 100%;
    transform: translate(-50%, -50%);
  }
}
