aside.cat-aside-narrow,
aside.cat-aside-wide {
    display: block;
    height: 100%;
    position: fixed;
    right: 0;
    top: 0;
    overflow: hidden;

    div.category-icon {
        height: $gr-ll * 100%;
        opacity: $gr-l;

        // span.category-icon {
        //     animation: rotateThis 1s linear;
        // }
    }

    h2.category-name {
        // animation: tracking-in-expand 1.7s cubic-bezier(0.215, 0.61, 0.355, 1) both;
        align-items: center;
        display: flex;
        justify-content: center;
        opacity: $gr-l;
    }

    nav.cat-aside-narrow,
    nav.cat-aside-wide {
        background: $nav-bg;
        bottom: 0;
        height: $gr-ll * 100%;
        position: absolute;
        width: 100%;
    }

    div.cat-aside {
        color: $cl;
        display: flex;
        flex-wrap: wrap;
        height: $cat-aside-space-h;
        justify-content: center;
    }
}

aside.cat-aside-narrow {
    background: $cat-aside-narr-bg;

    width: $cat-narr-w;

    div.category-icon {
        span.category-icon {
            width: $gr-hhl * 100%;

            img.category-icon {
                height: auto;
                width: 100%;
            }

            svg.cat-top {
                height: $gr-h * 100%;
            }
        }
    }

    h2.category-name {
        height: ($gr-h + $gr-lll + $gr-lllh) * 100%;
        position: relative;
        width: 100%;
        white-space: nowrap;

        span.category-name {
            transform: rotate(90deg);
            transform-origin: center;
        }
    }

    div.cat-aside-narrow-spacer {
        display: none;
    }
}

aside.cat-aside-wide {
    background: $cat-aside-wide-bg;
    width: $cat-wide-w;

    div.category-icon {
        span.category-icon {
            height: $gr-hhh * 100%;
        }
    }

    h2.category-name {
        height: $gr-ll * 100%;
        margin: 0 auto;
    }

    div.cat-aside-wide-spacer {
        height: $cat-aside-space-h;
    }

    div.cat-aside {
        margin: 0 auto;
        width: $gr-hhl * 100%;

        div.drinks-count {
            height: $gr-h * 100%;
        }

        button.btn {
            font-size: $gr-hh * 100%;
            margin-bottom: 1em;
            width: $gr-hh * 100%;
        }
    }
}
