@import "../../app/app-variables";
@import "../../app/app";
@import "../../categories/_categories";
@import "../_drinks";

div.drink-custom-hdr {
    align-items: center;
    background: $cat-custom-c1;
    // background-image: kbrdBg($cat-custom-c1, $cat-custom-c2);
    display: flex;
    height: $gr-ll * 100%;
    justify-content: center;
    text-align: center;
}

ul.ings-custom {
    display: flex;
    flex-wrap: wrap;
    height: ($gr-h + $gr-lh) * 100%;
    justify-content: space-around;
    list-style: none;
    width: 100%;

    li.ings-custom {
        align-items: center;
        display: flex;
        flex-basis: 33%;
        flex-grow: 2;
        justify-content: center;
        max-width: $gr-h * 100%;
        position: relative;
        text-align: center;
    }

    li.ings:nth-child(odd) {
        background: $bg-dark;
        color: $cl;
    }

    li.ings:nth-child(even) {
        background-color: $bg-light;
        color: $cd;
    }
}
