@import "../../app/app-variables";
@import "../../app/app";
@import "../../categories/_categories";

ul.ings-wanted,
ul.ings-wanted-not {
    li.ings-wanted,
    li.ings-wanted-not {
        span.ing-prefs {
            align-items: center;
            display: flex;
            height: 100%;
            justify-content: center;
            position: relative;
            text-align: center;
            width: 100%;
        }
    }
}

ul.ings-wanted {
    li.ings-wanted {
        span.ing-prefs-wanted {
            background-image: url(../../img/ings-prefs-want.svg);
            background-position: center center;
            background-repeat: no-repeat;
            background-size: ($gr-hh * 100%) ($gr-hh * 100%);
            color: $cat-prefs-c1;
            font-weight: bold;
        }
    }
}

ul.ings-wanted-not {
    li.ings-wanted-not {
        span.ing-prefs-wanted-not {
            background-image: url(../../img/ings-prefs-want-not.svg);
            background-position: center center;
            background-repeat: no-repeat;
            background-size: ($gr-hh * 100%) ($gr-hh * 100%);
            color: $cat-prefs-c2;
            text-decoration: line-through;
        }
    }
}
