@import "../app/app-variables";
@import "../app/app";

ul.categories {
    align-content: space-around;
    display: flex;
    flex-flow: row wrap;
    height: 100%;
    justify-content: space-around;
    max-height: 100%;

    li.categories:nth-child(odd) {
        background: $bg-dark;
        color: $cl;
    }

    li.categories:nth-child(even) {
        background-color: $cl;
        color: $cd;
    }

    li.categories {
        cursor: pointer;
        display: block;
        flex-grow: 2;
        flex-basis: 30%;
        height: 50%;
        max-height: 50%;
        max-width: 50%;
        min-height: 50%;
        overflow: hidden;
        position: relative;

        div.category-icon {
            height: $gr-h * 100%;

            span.category-icon {
                height: $gr-h * 100%;
            }
        }

        h2.category-name {
            align-items: center;
            display: flex;
            height: $gr-l * 100%;
            justify-content: center;
        }
    }
}

ul.categories-inactive {
    filter: grayscale(1);
}
