@import "../../app/app-variables";
@import "../../app/app";
@import "../../categories/_categories";
@import "../_drinks";

div.drink-prefs-hdr {
    align-items: center;
    background: $cat-prefs-c1;
    // background-image: kbrdBg($cat-prefs-c1, $cat-prefs-c2);
    // background-image: linear-gradient(215deg, $cat-prefs-c2, $cat-prefs-c1);
    background-image: linear-gradient(215deg, $cat-prefs-c2, $cat-prefs-c1);
    display: flex;
    height: $gr-ll * 100%;
    justify-content: center;
    text-align: center;
}

div.drink-prefs-sub-hdrs {
    background-image: linear-gradient(215deg, $cat-prefs-c2, $cat-prefs-c1);
    height: $gr-lll * 100%;

    span.drink-prefs-sub-hdrs {
        display: inline-block;
        height: 100%;
        text-align: center;
        width: 50%;
    }

    span.drink-prefs-sub-hdrs:first-of-type {
        background: $cl;
        color: $cat-prefs-c1;
        text-decoration: underline;
    }

    span.drink-prefs-sub-hdrs:last-of-type {
        background: $cd;
        color: $cat-prefs-c2;
        text-decoration: underline;
    }
}

ul.ings-wanted,
ul.ings-wanted-not {
    display: flex;
    flex-wrap: wrap;
    float: left;
    height: ($gr-h + $gr-ll + $gr-lllh) * 100%;
    justify-content: space-around;
    list-style: none;
    width: 50%;

    li.ings {
        flex-grow: 2;
        max-width: $gr-h * 100%;
        position: relative;
        width: 50%;
    }
}

ul.ings-wanted {
    background-color: $cl;
}

ul.ings-wanted-not {
    background: $bg-dark;
    color: $cl;
}
