@import "../../app/app-variables";
@import "../../app/app";

@keyframes rotateThis {
    from {
        transform: rotate(0deg) scale($gr-h);
    }

    to {
        transform: rotate(360deg) scale(1);
    }
}

div.drinks-count {
    align-content: space-evenly;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    position: relative;
    width: 100%;

    h4.drinks-count {
        position: relative;
        text-align: center;
        width: 100%;
    }

    h3.drinks-count {
        align-items: center;
        // animation: rotateThis 1s linear;
        border-radius: 50%;
        display: flex;
        height: $grs-lh * 2em;
        justify-content: center;
        position: relative;
        width: $grs-lh * 2em;
    }
}
